import React, { useEffect, useState } from "react";

function SafakPage(props) {

    const yenilemeHizi = 1;

    const [countText, setCountText] = useState("-");
    const [percentageText, setPercentageText] = useState("0");
    const [emojiText, setEmojiText] = useState("");

    function percentage(partialValue, totalValue) {
        return (100 * partialValue) / totalValue;
    }

    useEffect(() => {
        setInterval(() => {
            const totalDay = 365;
            const hedefTarih = new Date('12/21/2021'); // AA/GG/YYYY

            var simdikiZaman = new Date().getTime();
            var uzaklik = hedefTarih - simdikiZaman;

            var gun = Math.floor(uzaklik / (1000 * 60 * 60 * 24));
            var yuzde = (100 - percentage(gun, totalDay)).toFixed(1);

            setCountText(gun);
            setPercentageText("%" + yuzde);

            if(yuzde >= 0 && yuzde < 20) {
                setEmojiText('ðŸŒ‘');
            } else if(yuzde >= 20 && yuzde < 40) {
                setEmojiText('ðŸŒ’');
            } else if(yuzde >= 40 && yuzde < 60) {
                setEmojiText('ðŸŒ“');
            } else if(yuzde >= 60 && yuzde < 80) {
                setEmojiText('ðŸŒ”');
            } else if(yuzde >= 80) {
                setEmojiText('ðŸŒ•');
            }
        }, yenilemeHizi * 1000);
    });

    return (
        <>
            <p>Son <span id="count">{countText}</span> gÃ¼n</p>
            <span id="percentage">{percentageText}</span>
            <span id="emoji">{emojiText}</span>
        </>
    );
}

export default SafakPage;