import smdLogo from '../assets/img/socialmediabadges_photo.png';

function ProjectsPage(props) {
    return (
        <>
            <div className="projects">
                <div className="card">
                    <img src={smdLogo} alt="" />
                    <a href="https://github.com/ccopperr/social_media_badges" target="_blank" rel="noreferrer"><h1>Social Media Badges</h1></a>
                    <p>Simple Social Media Badges for websites.</p>
                </div>
            </div>
        </>
    );
}

export default ProjectsPage;